.footer {
  &__container {
    justify-content: space-between;
    padding-top: 6.5rem;
    padding-bottom: 6.5rem;
  }

  &__logo {
    flex: 0 0 180px;
    justify-content: center;
    height: 70px;

    img {
      display: block;
      margin: auto;
    }
  }

  &__information {
    flex: 0 1 21.2%;
  }

  &__title {
    margin-bottom: 0.6rem;
    font-weight: 700;
  }

  &__contacts {
    flex: 0 0 50%;
  }

  &__contacts-box {
    flex-wrap: wrap;
    align-items: center;
    color: $eighthcolor;
  }

  &__tel {
    margin-right: 2.5rem;
    margin-bottom: 1.5rem;
  }

  &__btn {
    flex: 0 0 50%;
    margin-bottom: 1.5rem;
  }

  &__mail {
    margin-right: 1.5rem;
  }

  &__geo {
    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  @include mw1 {
    &__container {
      padding-top: 3rem;
      padding-bottom: 3rem;
    }

    &__contacts {
      flex: 0 0 33.333%;
    }

    &__btn {
      flex: 0 0 100%;
    }
  }

  @include mw3 {
    &__container {
      flex-wrap: wrap;
    }

    &__logo {
      flex: 1 1 100%;
      justify-content: center;
      margin-bottom: 2.5rem;
    }

    &__information {
      flex-basis: 35%;
      padding-right: 1rem;
    }

    &__contacts {
      flex-basis: 50%;
    }
  }

  @include mw4 {
    &__title {
      text-align: center;
    }

    &__information {
      flex-basis: 100%;
      margin-bottom: 2rem;
    }

    &__contacts {
      flex-basis: 100%;
    }

    &__contacts-box {
      justify-content: center;
    }
  }
}

.user-information {
  &__link {
    text-decoration: underline;
  }

  @include mw4 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    &__item {
      &:not(:last-child) {
        margin-right: 1rem;
      }
    }
  }
}

