.benefits-mini {
  flex-wrap: wrap;

  &__item {
    align-items: center;

    &:not(:last-child) {
      margin-right: 2.8rem;
    }
  }

  &__icon {
    margin-right: 1rem;
    width: 6rem;
    height: 5.9rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    display: inline-block;
    max-width: 180px;
    line-height: 1.77;
  }

  @include mw4 {
    &__item {
      &:not(:last-child) {
        margin-right: 0;
      }
    }

    &__text {
      max-width: none;

      br {
        display: none;
      }
    }
  }
}

.benefits {
  flex-wrap: wrap;

  &__item {
    flex: 0 1 calc(33.3333% - 20px);
  }

  &__icon {
    margin-bottom: 1.5rem;
    width: 6.4rem;
    height: 6.4rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    line-height: 1.9;
  }

  @include mw3 {
    &__item {
      flex: 0 1 calc(50% - 20px);
    }

    &__icon {
      margin-bottom: 0.4rem;
    }
  }

  @include mw4 {
    justify-content: space-between;

    &__item {
      display: flex;
      flex-basis: calc(100% - 20px);
      align-items: center;
    }

    &__icon {
      flex-shrink: 0;
      margin: 0 0.8rem 0 0;
      width: 5rem;
      height: 5rem;
    }
    &__text {
      line-height: 1.4;
    }
  }
}
