
.thumbs {
  position: relative;
  margin: -10px;

  &__col {
    flex-basis: calc(15.3% + 20px);
    flex-wrap: wrap;

    &--big {
      flex-basis: calc(32.2% + 20px);
    }

    &--reverse {
      flex-wrap: wrap-reverse;
    }
  }

  &__img {
    position: relative;
    margin: 10px;
    width: 100%;
    height: 169px;
    cursor: pointer;

    &::before,
    &::after {
      content: '';
      position: absolute;
      opacity: 0;
      transition: opacity 0.3s linear;
    }

    &::before {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: $thirdcolor1;
    }

    &::after {
      left: 50%;
      top: 50%;
      width: 50px;
      height: 50px;
      background-image: url('../images/svgSprite/svgSprite.svg#add');
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
    }

    @include hover {
      &:hover {
        &::before {
          opacity: 0.7;
        }

        &::after {
          opacity: 1;
        }
      }
    }
  }

  &__img--big {
    height: 338px;
  }

  @include mw1 {
    margin: -5px;

    &__img {
      margin: 5px;
    }
  }

  @include mw2 {
    &__img {
      height: 130px;

      &--big {
        height: 260px;
      }
    }
  }

  @include mw3 {
    
    &__col {
      flex: 0 0 150px;

      &--big {
        flex: 0 0 300px;
      }
    }

    &__img {
      height: 100px;

      &--big {
        height: 200px;
      }
    }
  }

  @include mw4 {
    &__col {
      flex: 0 0 30%;
      min-width: 120px;

      &--big {
        flex: 0 0 50%;
        min-width: 175px;
      }
    }
  }
}