
.reputation {
  &__subtitle {
    margin-bottom: 6rem;
  }

  &__list {
    margin-bottom: 9rem;
  }

  @include mw1 {
    &__list {
      margin-bottom: 7rem;
    }
  }

  @include mw2 {
    &__subtitle {
      margin-bottom: 3.5rem;
    }

    &__list {
      margin-bottom: 5.5rem;
    }
  }

  @include mw3 {
    &__subtitle {
      margin-bottom: 1.5rem;
    }

    &__list {
      margin-bottom: 2.5rem;
    }
  }

  @include mw4 {
    &__list {
      margin-bottom: 1.5rem;
    }
  }
}
